#{$root-selector} {

  a,
  a.#{$ns}-button,
  .#{$ns}-button,
  .#{$ns}-html-select select,
  .#{$ns}-select select {
    font-weight: var(--font-weight-bold);

    transition: background-color 100ms linear;

    &:active {
      transition-duration: 50ms;
    }
  }

  .#{$ns}-button.#{$ns}-minimal {
    .#{$ns}-icon {
      //:not(:only-child) {
      color: var(--pt-intent-primary-hover);
    }

    &:hover {
      color: var(--pt-intent-primary-hover);
    }
  }

