@charset "UTF-8";
html{
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
}

*,
*::before,
*::after{
  -webkit-box-sizing:inherit;
          box-sizing:inherit;
}

body{
  font-size:14px;
  font-weight:400;
  letter-spacing:0;
  line-height:1.2858142857;
  text-transform:none;
  color:#1c2127;
  font-family:-apple-system, "BlinkMacSystemFont", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Open Sans", "Helvetica Neue", "blueprint-icons-16", sans-serif;
}

p{
  margin-bottom:10px;
  margin-top:0;
}

small{
  font-size:12px;
}

strong{
  font-weight:600;
}

::-moz-selection{
  background:rgba(125, 188, 255, 0.6);
}

::selection{
  background:rgba(125, 188, 255, 0.6);
}
.bp4-icon{
  display:inline-block;
  -webkit-box-flex:0;
      -ms-flex:0 0 auto;
          flex:0 0 auto;
  vertical-align:text-bottom;
}
.bp4-icon:not(:empty)::before{
  content:"" !important;
  content:unset !important;
}
.bp4-icon > svg{
  display:block;
}
.bp4-icon > svg:not([fill]){
  fill:currentcolor;
}

span.bp4-icon-standard{
